.particles-js, .home-container{
  @media (min-width: 1024px) {
    height: 92vh;
  }
  @media (max-width: 1024px) {
    height: 100vh;
  }
}

.particles-js{
    //background: linear-gradient(-45deg, rgba(253,29,29,1), rgba(63,94,251,1), #23a6d5, #23d5ab);
    background: radial-gradient(#1bf0f1, rgba(63,94,251,1));
    background-size: 400% 400%;
    animation: gradient 10s ease infinite;
    -webkit-animation: gradient 12s ease infinite;
}

.home-container{
  width: 100%;
  position: absolute;
  color: #fff;
  h1,h2{
    color: #fff;
  }

  display: inline;
}

@keyframes gradient {
  0%{background-position:0% 50%}
  25%{background-position:10% 50%}
  50%{background-position:100% 5%}
  75%{background-position:100% 50%}
  100%{background-position:0% 50%}
}